import React from 'react'
import Navbar from './Navbar';
import { Map, GoogleApiWrapper, Marker, InfoWindow } from 'google-maps-react';
import { Component } from 'react';
import '../index.css'
import Button from './Button';
import ButtonRM from './ButtonRM'
import { useNavigate } from 'react-router-dom';



export class MapFrame extends Component {
   

    constructor(props) {
      super(props);
  
      this.state = {
        points: [],
        dataLoaded: false,
        count: 0
      }
    }

    componentDidMount() {
      

        fetch("http://artemisbalance.herokuapp.com/api/user/get_points") 
            .then(res => res.json())
            .then((json) => {
              this.setState({
                  points: json,
                  dataLoaded: true,

              });
        })
        this.setState({
          count: this.state.points.length
      });
    }

  
    displayMarkers = () => {
      return this.state.points.map((store, index) => {
        this.state.count = this.state.count+1;
        return <Marker key={index} id={index} position={{
         lat: store.latitude,
         lng: store.longitude,
       }}
       onClick={() => this.handleClick(index)} />
      })
    }

    handleClick = (e) => {
      if (this.props.Rem) {
        fetch("https://artemisbalance.herokuapp.com/api/user/remove_point", {
          method: 'post',
          headers: {
            'Accept': 'application/json',
            'Content-type': 'application/json',
            'auth-token' : 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJfaWQiOiI2MWI3NWRjNWM0NWEyNzAwMTZjNzdmMzkiLCJpYXQiOjE2NDI3MjU4MDl9.3qc-0g3X2JGlfgazoPO9lapa0EH8P-1gbYtCGKUtm5A'
          },
          body: JSON.stringify({
            point_id: e
          })
        });
        //POST request
        this.forceUpdate()
      }
      
    }
    
    onMapClicked = (e, map, coor) => {
      console.log(this.state.count/2)
      const { latLng } = coor;
      const lat = latLng.lat();
      const lng = latLng.lng();
      if (this.props.Add) {
        //Post Method
        fetch("http://artemisbalance.herokuapp.com/api/user/add_point", {
          method: 'post',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'auth-token' : 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJfaWQiOiI2MWI3NWRjNWM0NWEyNzAwMTZjNzdmMzkiLCJpYXQiOjE2NDI3MjU4MDl9.3qc-0g3X2JGlfgazoPO9lapa0EH8P-1gbYtCGKUtm5A',
          },
          body: JSON.stringify({
            Id: this.state.count/2,
            point_latitude: lat,
            point_longitude: lng,
            storage: 5
          })
        });
      }
      
    }

    render() {
      const {dataLoaded, points} = this.state;
      console.log(points.length)

      if (!dataLoaded) {
        return <div className='NNN'>
          <h1>Please wait</h1>
        </div>
      }
      return (
        <div>          
              <Map
                google={this.props.google}
                zoom={10}
                style={mapStyles}
                initialCenter={{ lat: 40.81, lng: 29.36}}
                onClick={this.onMapClicked}
               >
              {this.displayMarkers()}
                </Map>
          </div>
          
        
          
            
            
        
            
            
         
      );
    }
  }
export default GoogleApiWrapper({
    apiKey: 'AIzaSyDrLlGDGGxdkkT9nsIeEkjenFjcRzkf-UM'
  })(MapFrame);

  const mapStyles = {
    width: '100%',
    height: '95%',
  };

